import listCell from '@/components/list-cell'
import clickButton from '@/components/click-button'
import { afterSalePage, submitAfterSale, uploadAwatar, BASE_URL } from '@/api'
import uniIcon from '@/components/uni-icon/uni-icon.vue'
export default {
	data() {
		return {
			imgList: [],
			applyType: '选择申请类型',
			applyTypeList: [], // 申请类型
			addressInfo: {},
			content: '',
			order_id: '',
			name: '',
			phone: '',
			address: ''
		};
	},
	onLoad (options) {
		this._getAfterSaleInfo(options.sn);
		// this.order_id = options.id;
	},
	methods: {
		async _getAfterSaleInfo (sn) {  // 申请详情
			uni.showLoading({
				title: '正在加载数据...'
			})
			let result = await afterSalePage ({
				token: uni.getStorageSync('token'),
				order_sn: sn,
				// order_id: id
			})
			uni.hideLoading()
			if(result.recode == 10000) {
				this.applyTypeList = result.data.type;
				this.addressInfo = result.data.order_sn;
			}else {
				this.applyTypeList = []
			}
		},
		async buttonClick () {  // 提交申请
			if(this.applyType == '选择申请类型') {
				uni.showToast({
					title: '请选择申请类型',
					icon: 'none'
				})
				return
			}
			if(this.name == '') {
				uni.showToast({
					title: '请输入联系人姓名',
					icon: 'none'
				})
				return;
			}
			if(this.phone == '') {
				uni.showToast({
					title: '请输入联系电话',
					icon: 'none'
				})
				return;
			}
			let result = await submitAfterSale ({
				token: uni.getStorageSync('token'),
				type: this.applyType,
				images: this.imgList.join(','),
				order_sn: this.addressInfo,
				content: this.content,
				// order_id: this.order_id,
				name: this.name,
				phone: this.phone,
				address: this.address
			})
			if(result.recode == 10000) {
				uni.showToast({
					title: '申请成功',
				})
				setTimeout(() => {
					uni.navigateBack()
				}, 1500)
			}else {
				uni.showToast({
					title: result.remsg,
					icon: 'none'
				})
			}				
		},
		addImg () {  // 添加图片
			let self = this;
			uni.chooseImage({
				count: 1,
				success: (chooseImageRes) => {
					const tempFilePaths = chooseImageRes.tempFilePaths;
					uni.uploadFile({
						url: BASE_URL + '/api/common/upload', 
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {
							token: uni.getStorageSync('token')
						},
						success: (uploadFileRes) => {
							console.log(JSON.stringify(uploadFileRes.data))
							let result = JSON.parse(uploadFileRes.data);
							if(result.recode == 20026) {
								self.imgList.push(BASE_URL + result.data.url);
							}
						}
					});
				}
			});
		},
		deleteImg (index) {  // 删除图片
			this.imgList.splice(index, 1);
		},
		cellClick () {        // 选择售后类型
			let self = this;
			uni.showActionSheet({
				itemList: self.applyTypeList,
				success: function (res) {
					self.applyType = self.applyTypeList[res.tapIndex]
				}
			});
		}
	},
	components: {
		listCell,
		clickButton,
		uniIcon
	}
}
